.order_c {
    border: 1px solid #222;
    width: 400px;
    height: 400px;
    margin: auto;
    background-color: #222;
}
.contact_app {
    margin-top: 10%;
}
.contact_app input {
    display: block;
    margin: auto;
    margin-top: 10%;
    height: 30px;
    padding-left: 10px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border: 1px solid orangered;
    background: none;
    color: aliceblue;
    outline: none;
}
.contact_app input::placeholder {
    color: orangered;
}

.contact_app button {
    display: block;
    margin: auto;
    height: 30px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin-top: 10%;
    cursor: pointer;
    background: linear-gradient(gold,orange);
}
.contact_app a {
    cursor: pointer;
    text-decoration: underline lightcoral 10px;
    text-align: center;
    display: block;
    color: aliceblue;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.login {display: block;}
.rej {display: none;}