body {
  background: url(./img/backgroundbottom.png) no-repeat;
  background-size: 99.99%;
  background-position: center 20%;
}
a {
  text-decoration: none;
  color: darkgreen;
}

nav {
  background: url(./img/adventuretime-characters.png) no-repeat;
  height: 20em;
  width: 100%;
  background-position: center center;
  background-size: 50%;
}
.nav_ds button {
  color: #222;
  display: block;
  margin: auto;
  width: 250px;
  height: 40px;
  font-family: cursive;
  border: none;
  font-size: large;
  background: linear-gradient(-45deg,limegreen,gold,orange);
}